<template>
  <div class="study2">
    <h2>{{title}}</h2>
    <section class="study2-ls">
      <div v-for="item in album" :key="item.src">
        <img :src="item.src" alt="">
      </div>
    </section>
    <div class="study2-pagination">
      <span v-show="pagination.current > 1" @click="getDetail(--pagination.current)">-</span>
      <span>{{pagination.current}} / {{pagination.total}}</span>
      <span @click="getDetail(++pagination.current)">+</span>
    </div>
  </div>
</template>

<script>
import { $fetch } from '@/store'

const DOMAIN = 'https://jrainlau.com/api/hentai'

export default {
  data () {
    return {
      href: '',
      title: '',
      album: []
    }
  },
  computed: {
    pagination () {
      const eg = this.album[0]
      const pag = {
        current: 0,
        total: 0
      }
      if (eg) {
        pag.current = eg.currentPage
        pag.total = eg.totalPages
      }
      return pag
    }
  },
  mounted () {
    document.body.querySelector('header').style.display = 'none'
    const { href, title } = this.$route.query
    this.href = href.replace(/^[/](.*)[/]$/g, '$1')
    this.title = title
    this.getDetail()
  },
  methods: {
    async getDetail (index = 1) {
      const result = await $fetch({
        url: `https://jrainlau.com/api/hentai/${localStorage.getItem('study_keyword')}/detail`,
        method: 'post',
        data: {
          href: this.href,
          index
        }
      }).catch(e => e.response)
      this.album = result.data
    }
  }
}
</script>

